import { CLoginModel, LoginService } from './login.service';
import { Component, OnInit } from '@angular/core';
import { ConstantsService } from 'src/app/services/constants.service';
import { UtilsService } from 'src/app/services/utils.service';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AppValueService } from 'src/app/services/app-value.service';
import { Storage } from '@ionic/storage';
import { LoadingService } from 'src/app/services/loading.service';
import { MenuController } from '@ionic/angular';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  loginInfo = { userName: '', password: '' };
  chkAutoLogin = false;
  constructor(public constantsService: ConstantsService,
    public utilsService: UtilsService,
    private loginService: LoginService,
    private router: Router,
    private translate: TranslateService,
    public appValueService: AppValueService,
    private storage: Storage,
    public loading: LoadingService,
    public menu: MenuController) {
    this.menu.enable(false);
    storage.get('chkAutoLogin').then((value) => {
      if (value) {
        this.chkAutoLogin = value;
        storage.get('userName').then((userName) => { this.loginInfo.userName = userName; });
        storage.get('password').then((password) => { this.loginInfo.password = password; });
      }
    });
  }

  ngOnInit() {
  }

  /**
  * Login method
  */
  login() {
    this.menu.enable(true);
    this.utilsService.loadingStart();
    if (this.chkAutoLogin) {
      this.storage.set('chkAutoLogin', this.chkAutoLogin);
      this.storage.set('userName', this.loginInfo.userName);
      this.storage.set('password', this.loginInfo.password);
    }
    else {
      this.storage.clear();
    }
    this.loginService.login(this.loginInfo).subscribe(allowed => {
      if (allowed) {
        this.menu.enable(true);
        this.utilsService.loadingStop();
        this.router.navigateByUrl(this.constantsService.PATH_PRODUCT_MANAGEMENT);
      } else {
        this.utilsService.showError("Sai tài khoản : " + this.loginInfo.userName);
      }
    },
      error => {
        this.utilsService.showError(error);
      });
  }
}
