import { Injectable } from '@angular/core';
import { ConstantsService } from 'src/app/services/constants.service';
import { UtilsService } from 'src/app/services/utils.service';
import { Observable, throwError } from 'rxjs';
import { AppValueService } from 'src/app/services/app-value.service';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireAuth } from '@angular/fire/auth';
import { auth } from 'firebase/app';
/**
  * This is the Model using for login screen
  */
export class CLoginModel {
  key: String;
  id: String = "";
  userName: String = "";
  active: boolean = true;
  ranked: String = "";
  image: String = "";
  constructor() {
  }
  /**
  * This is the function mapping object to model
  */
  copy(data: any) {
    this.key = data.key;
    this.id = data.id;
    this.userName = data.userName;
    this.active = data.active;
    this.ranked = data.ranked;
    this.image = data.image;
  }
}

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  path_Users = "Users/";
  path_IDLogin = "id/";
  path_PasswordLogin = "password/";
  constructor(public constantsService: ConstantsService,
    public utilsService: UtilsService,
    public db: AngularFireDatabase,
    public appValueService: AppValueService,
    public afAuth: AngularFireAuth) {
  }

  login(loginInfo: any) {
    let listUser: any;
    if (loginInfo.userName === null || loginInfo.password === null) {
      return Observable.throw("Điền thông tin");
    } else {
      return Observable.create(observer => {
        let access = false;
        let id: string = loginInfo.userName + "@gmail.com";
        this.utilsService.appValueService.APP_AUTHENTICATION = {
          id: "",
          userName: "",
          active: "",
          ranked: "",
          image: ""
        };
        this.afAuth.auth.signInWithEmailAndPassword(id, loginInfo.password)
          .then((authenticated) => {
            listUser = this.db.list(this.path_Users,
              ref => ref.orderByChild(this.path_IDLogin)
                .startAt(authenticated.user.uid)
                .endAt(authenticated.user.uid));
            listUser.valueChanges().subscribe(userInfo => {
              if (userInfo.length == 0) {
                this.utilsService.appValueService.APP_AUTHENTICATION = {
                  id: "",
                  userName: "",
                  active: "",
                  ranked: "",
                  image: ""
                };
                observer.next(access);
                observer.complete();
              }
              else {
                access = true;
                userInfo.forEach(userLogin => {
                  this.utilsService.appValueService.APP_AUTHENTICATION = userLogin;
                  this.utilsService.setAppAuthentication();

                }) 
                observer.next(access);
                observer.complete();
              }
            });
          })
          .catch(error => {
            this.utilsService.showError(error);
          });
      });
    }
  }
}
